@import "vendors/include-media";

.my-rating {
    display: flex;
    align-items: center;
}

.rating-field{
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 175px;
	@include media(">=desktop-lg") {
        margin-left: auto;
        margin-right: auto;
    }
    &-label{
        margin-bottom: 5px;
        text-transform: uppercase;
        color: $dark-700;
    }
}

.jq-stars {
    display: inline-block;
}

.jq-rating-label {
    font-size: 22px;
    display: inline-block;
    position: relative;
    vertical-align: top;
    font-family: helvetica, arial, verdana;
}

.jq-star {
    width: 100px;
    height: 100px;
    display: inline-block;
    cursor: pointer;
}

.jq-star-svg {
    padding-right: 3px;
    width: 100%;
    height: 100%;
}

.jq-star-svg path {
    stroke-linejoin: round;
}

.jq-shadow {
    -webkit-filter: drop-shadow(-2px -2px 2px #888);
    filter: drop-shadow(-2px -2px 2px #888);
}
